@import "../settings/breakpoints.css";

.article-layout {
  --spacer: 30px;

  display: grid;
  margin-top: 60px;
  grid-gap: var(--spacer);
  grid-template-columns: 1fr;
  grid-template-areas:
    "main"
    "aside"
    "footer";

  @media (--article-breakpoint) {
    margin-top: 120px;
    grid-template-columns: 2fr 1fr;
    grid-template-areas:
      "main aside"
      "footer footer";
  }
}

.article-main {
  grid-area: main;
}

ul.article-main__tag-list {
  margin-top: 10px;
}

.article-aside {
  grid-area: aside;
  text-align: center;

  & > * + * {
    margin-top: var(--spacer);
  }

  @media (--article-breakpoint) {
    text-align: left;
  }
}

.article-list {
  & > * + * {
    margin-top: var(--spacer);
  }
}

.article-navigation {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: var(--spacer);

  & a {
    text-decoration: none;
  }

  & > div:nth-child(1) {
    text-align: left;
  }

  & > div:nth-child(2) {
    text-align: right;
  }
}

.template-article {
  & .featured-collection {
    grid-area: footer;
  }
}

/* Form */
.comment-form {
  display: grid;
}
